.fadeUp {
	transform: translate(0,20px);
	opacity: 0;
	transition-duration: 1s;
}

.fadeDown {
	transform: translate(0,-20px);
	opacity: 0;
	transition-duration: 1s;
}

.rollLeft {
	transform: scale(0,1);
	overflow: visible;
	opacity: 0;
	transform-origin: 0 0;
	transition-duration: 1s;

}


.rollTop {
	transform: scale(1,0);
	overflow: visible;
	opacity: 0;
	transform-origin: 0 0;
	transition-duration: 1s;

}


.section {
	transition-duration: 1s;
	opacity: 0;
	&.animated {
		opacity: 1;
		.fadeUp,.fadeDown {
			transform: translate(0,0);
			opacity: 1;
		}
		.rollLeft {
			transform: scale(1,1);
			opacity: 1;
		}
		.rollTop {
			transform: scale(1,1);
			opacity: 1;
		}
	}
	&.animation_ends {
		.step1,.step2,.step3,.step4,.step5,
		.step6,.step7,.step8,.step9,.step10,
		.step11,.step12,.step13,.step14 {
			transition-delay: 0s !important;	
		}
	}
}